<template>
    <!-- 手机验证登录 -->
    <div>
        <div class="wyl_login_img">
            <img src="../../assets/Login/login.png" alt=""/>
        </div>
        <div class="wyl_register">
            <van-field v-model="sms" center clearable placeholder="请输入手机号">
                <template #button>
<!--                  发送前的样式-->
                    <van-button size="small"
                                v-show="isShow===false"
                                type="warning"
                                @click="onClickSend"
                                :class="isShow?'wpf-daojishi-active':'wpf-daojishi'">发送验证码</van-button>
<!--                  发送后的样式-->
                  <van-button size="small"
                              v-show="isShow===true"
                              type="default"
                              :class="isShow?'wpf-daojishi-active':'wpf-daojishi'">验证码{{num}}</van-button>
                </template>
            </van-field>
            <van-cell-group>
                <van-field v-model="sss" placeholder="请输入短信验证码"/>
            </van-cell-group>
        </div>
        <div class="wyl_register_pass">
            <div>*未注册的手机号将自动注册</div>
            <div @click="onLogin">使用密码登录</div>
        </div>
        <div class="wyl_login_button">
            <button @click="redister">登录</button>
        </div>
    </div>
</template>

<script>
    import {Toast} from 'vant';

    export default {
        data() {
            return {
                sms: "",
                sss: "",
              num:60,
              isShow:false
            };
        },
        methods: {
            onLogin() {
                this.$router.push("/login");
            },

            //点击发送验证码
            onClickSend() {
                this.$APP.smsCode({
                    mobile: this.sms,
                    sms_type: "login",
                }).then(res => {
                  console.log(res)
                })
              this.countDown()
            },
            //点击登录
            redister() {
                this.$APP
                    .login({
                        mobile: this.sms,
                        sms_code: this.sss,
                        client: 1,
                        type: 2,
                    })
                    .then((res) => {
                        console.log(res);
                        let token = res.data.data.remember_token;
                        window.localStorage.setItem("token", token);
                        this.$router.push("/edit");
                    });
            },
          //倒计时
          countDown(){
              this.isShow=true
            setInterval(()=>{
              this.num--  //倒计时开始倒减
              if (this.num<=0){
                this.isShow=false
                this.num=60
              }
            },1000)
          }
        },
    }
</script>

<style scoped>
    .wyl_register {
        width: 100%;
        height: 15vh;
    }

    .wyl_login_img {
        width: 100%;
        height: 30vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .wyl_login_img img {
        width: 80%;
    }

    .wyl_login_button {
        width: 100%;
        height: 10vh;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        font-size: 0.8rem;
    }

    .wyl_login_button button {
        width: 90%;
        height: 7vh;
        background: orange;
        border-radius: 18px;
        border: none;
    }

    .wyl_register_pass {
        width: 100%;
        height: 12vh;
        display: flex;
        align-items: center;
        justify-content: space-around;
        font-size: 0.6rem;
        color: gray;
    }
  .wpf-daojishi{
    color: red;
  }
    .wpf-daojishi-active{
      color: #595959;
    }
</style>